import { defineStore } from 'pinia';
import { getDatasets, createDataset, updateDataset, deleteDataset, deleteBatchDatasets, bindSamplesByName as addSamplesToDataset, unbindSample } from '@/api/dataset';
import type { Dataset, GetDatasetsParams, CreateDatasetParams, UpdateDatasetParams, DeleteDatasetParams, DeleteBatchDatasetsParams, AddSamplesToDatasetParams, UnbindSampleParams } from '@/api/dataset';

export const useDatasetStore = defineStore('dataset', {
  state: () => ({
    datasets: [] as Dataset[],
    total: 0,
    loading: false,
    lastFetchTime: 0,
    currentProjectId: null as number | string | null,
    currentParams: {} as GetDatasetsParams
  }),
  
  getters: {
    getDatasetById: (state) => (id: number) => {
      return state.datasets.find(dataset => dataset.id === id);
    }
  },
  
  actions: {
    async fetchDatasets(projectId: number | string, params: Partial<GetDatasetsParams> = {}) {
      // 如果已经有数据且在短时间内（如5秒）请求过，且参数相同，直接返回缓存
      const now = Date.now();
      const isSameParams = JSON.stringify({ ...this.currentParams, ...params }) === 
                          JSON.stringify(this.currentParams);
      
      if (
        this.datasets.length > 0 && 
        this.currentProjectId === projectId &&
        now - this.lastFetchTime < 5000 && 
        isSameParams &&
        !params.forceRefresh
      ) {
        return { datasets: this.datasets, total: this.total };
      }
      
      this.loading = true;
      try {
        const requestParams: GetDatasetsParams = {
          project_id: projectId as number,
          ...params
        };
        
        delete requestParams.forceRefresh;
        this.currentParams = requestParams;
        
        const response = await getDatasets(requestParams);
        
        if (response.data.code === 200) {
          this.datasets = response.data.data.datasets || [];
          this.total = response.data.data.total || 0;
          this.lastFetchTime = now;
          this.currentProjectId = projectId;
        }
        return { datasets: this.datasets, total: this.total };
      } catch (error) {
        console.error('获取数据集列表失败:', error);
        throw error;
      } finally {
        this.loading = false;
      }
    },
    
    async createDataset(params: CreateDatasetParams) {
      const response = await createDataset(params);
      if (response.data.code === 200) {
        // 强制刷新数据集列表
        await this.fetchDatasets(params.project_id, { forceRefresh: true });
      }
      return response;
    },
    
    async updateDataset(params: UpdateDatasetParams) {
      const response = await updateDataset(params);
      if (response.data.code === 200) {
        // 更新本地状态
        const index = this.datasets.findIndex(dataset => dataset.id === params.id);
        if (index !== -1) {
          this.datasets[index] = { ...this.datasets[index], ...response.data.data };
        }
      }
      return response;
    },
    
    async deleteDataset(params: DeleteDatasetParams) {
      const response = await deleteDataset(params);
      if (response.data.code === 200) {
        // 更新本地状态
        this.datasets = this.datasets.filter(dataset => dataset.id !== params.dataset_id);
        if (this.total > 0) this.total--;
      }
      return response;
    },
    
    async deleteBatchDatasets(params: DeleteBatchDatasetsParams) {
      const response = await deleteBatchDatasets(params);
      if (response.data.code === 200) {
        // 更新本地状态，移除已删除的数据集
        this.datasets = this.datasets.filter(dataset => !params.dataset_ids.includes(dataset.id));
        // 更新总数
        this.total = Math.max(0, this.total - params.dataset_ids.length);
      }
      return response;
    },
    
    async addSamplesToDataset(params: AddSamplesToDatasetParams) {
      const response = await addSamplesToDataset(params);
      if (response.data.code === 200) {
        // 更新本地状态，增加样本数量
        const index = this.datasets.findIndex(dataset => dataset.id === params.dataset_id);
        if (index !== -1) {
          this.datasets[index].sample_count += params.sample_ids.length;
        }
      }
      return response;
    },
    
    /**
     * 从数据集中移除样本
     * @param params 移除样本参数
     * @returns 移除结果
     */
    async unbindSampleFromDataset(params: UnbindSampleParams) {
      const response = await unbindSample(params);
      if (response.data.code === 200) {
        // 更新本地状态，减少样本数量
        const index = this.datasets.findIndex(dataset => dataset.id === params.dataset_id);
        if (index !== -1 && this.datasets[index].sample_count > 0) {
          this.datasets[index].sample_count -= 1; // 减少1个样本
        }
      }
      return response;
    }
  }
}); 